.ball {
  background-color: black;
  color: white;
  text-align: center;
  font-family: monospace;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  padding: 20%;
  margin: 100px auto;
  -webkit-box-shadow: 0 50px 10px rgba(0,0,0,0.3), 0 30px 50px rgba(0,0,0,0.3);
          box-shadow: 0 50px 10px rgba(0,0,0,0.3), 0 30px 50px rgba(0,0,0,0.3);
}

.input {
  height: 40px;
  width: 180px;
  font-size: 20px;
  font-family: sans-serif;
  text-align: center;
  color: white;
  background-color: black;
  margin-top: 10px;
}

@media screen and (min-width: 1201px) {
  .answer {
    font-size: 40px;
  }
}

@media screen and (max-width: 1200px) {
  .answer {
    font-size: 24px;
  }
}

.shake {
  -webkit-animation: shake 0.9s cubic-bezier(.36,.07,.19,.97) both;
          animation: shake 0.9s cubic-bezier(.36,.07,.19,.97) both;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

@-webkit-keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
}

@keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
}

.fadeIn {
  opacity: 0;
  -webkit-animation: fadeIn 2s forwards;
          animation: fadeIn 2s forwards;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}



